module.exports = "<div class=example>{{ msg }}</div> <br> <div class=\"ui center aligned container\"> <div class=\"ui two column divided grid\"> <div class=row> <div class=\"six wide column\"> <div class=\"ui segment\"> 2 </div> </div> <div class=\"ten wide column\"> <div class=\"ui segment\"> <div class=\"ui input\"> <input type=text placeholder=\"What is your mind?\" v-model=status> <div class=\"ui animated button\" tabindex=0 v-on:click=addPost(status)> <div class=\"visible content\">POST</div> <div class=\"hidden content\"> <i class=\"right arrow icon\"></i> </div> </div> </div> </div> <div v-for=\"tmp in data\" class=centered> <div class=\"ui card\" style=width:100%;margin-bottom:14px> <div class=content> <div class=\"right floated meta\">{{formatdate(tmp.date)}}</div> <img class=\"ui avatar image\" src=/static/{{tmp.pic}}> {{tmp.name}} </div> <div class=image> <img> </div> <div class=content> {{tmp.message}} <span class=\"right floated\"> <i class=\"heart outline like icon\"></i> {{tmp.love.length}} likes </span> </div> <div class=\"extra content\"> <div class=\"ui comments\"> <h3 class=\"ui dividing header\">Comments</h3> <div class=comment> {{formatdate(tmp.date)}} <div class=content v-for=\"comment in tmp.comment\"> <a class=author>{{comment.name}}</a> <div class=metadata> <span class=date>{{formatdate(comment.date)}}</span> </div> <div class=text> {{comment.message}} </div> </div> </div> <form class=\"ui reply form\"> <div class=field> <textarea v-model=tmp.newComment></textarea> </div> <div class=\"ui blue labeled submit icon button\" v-on:click=\"addcomment($index, tmp.newComment)\"> <i class=\"icon edit\"></i> Add Reply </div> </form> </div> </div> </div> </div> </div> </div> </div> </div>";


